.myPokeParty {
  background: rgb(219, 219, 219);
  max-width: 500px;

  [class*="poke"] {}

  .poke {
    @at-root &_head {
      position: relative;
      @at-root &_name {}

      @at-root &_img {
        border-radius: 100%;
        width: 100px;
        height: 100px;
        background: white;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          max-width: 95%;
        }
      }
      //end img code

      @at-root &_ball {}
      //end ball code

      @at-root &_item {}
      //end item code
    }
    //end head code

    @at-root &_info {
      position: grid;
    }
  }
}
